.homepage-content{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 96vw;
    height: 68vh;
    margin-left: auto;
}

.homepage-container h1{
    margin: 0;
}

.homepage-form{
    display: flex;
    flex-direction: column;
    margin-top: 10px;
}

.homepage-form button{
    margin: 0 auto;
    margin-top: 10px;
}

.homepage-form-error{
    position: relative;
    margin-top: 5px;
    text-align: center;
    font-size: 18px;
    color: #ec2a2a;
}