.dashboards-content{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 83%;
  margin-left: auto;
}

.dashboards-items{
  display: flex;
  flex-wrap: wrap;
  max-width: 100%;
  margin: 0 auto;
  justify-content: flex-end;
}

.dashboards-item{
  position: relative;
  width: 10em;
  margin: 10px;
  height: 7em;
  background-color: #2626d8;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1;
}

.dashboards-item a{
  color: #fff;
  text-decoration: none;
  font-size: 20px;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.dashboards-item:focus{
  transform: scale(1.05);
}

.dashboards-item:hover{
  cursor: pointer;
}

.dashboards-item-close {
  z-index: 10;
  position: absolute;
  right: 0;
  top: 5px;
  width: 32px;
  height: 20px;
  opacity: 0.5;
  }

.dashboards-item-close:hover {
  cursor: pointer;
  opacity: 1;
}
.dashboards-item-close:before, .dashboards-item-close:after {
  position: absolute;
  left: 15px;
  content: ' ';
  height: 20px;
  width: 2px;
  background-color: #fff;
}

.dashboards-item-close:before {
  transform: rotate(45deg);
}

.dashboards-item-close:after {
  transform: rotate(-45deg);
}

.dashboards-error{
  position: relative;
  margin-top: 5px;
  text-align: center;
  font-size: 18px;
  color: #ec2a2a;
}

.dashboard-card{
  margin-left: 5px;
}

.dashboard-content{
  display: flex;
  flex-wrap: wrap;
}