.register-container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 99vw;
    height: 60vh;
    margin-left: auto;
}

.register-form{
    display: block;
    display: flex;
    flex-direction: column;
    margin-top: 10px;
}

.register-form button{
    margin-top: 5px;
}

.register-form a{
    text-align: center;
    margin-top: 5px;
}

.register-form-error{
    position: relative;
    margin-top: 5px;
    text-align: center;
    font-size: 18px;
    color: #ec2a2a;
}