.dashboard-content{
    display: flex;
}

.dashboard-create{
    width: 8em;
    max-height: 3em;
    border: none;
    margin-left: 5px;
    border-radius: 5px;
}

.dashboard-card-create{
    margin-top: 0.5em;
    max-width: 18em;
    margin-bottom: 10px;
    border-radius: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 5px;
}

.dashboard-card-create-wrapper{
    padding: 5px;
}

.dashboard-card-create-header{
    border-radius: 5px;
    width: 100%;
    height: 100%;
    background-color: #2b2bd0;
    max-height: 2em;
}

.dashboard-card-create input{
    width: 98%;
    border-radius: 3px;
    border: none;
    padding: 2px;
}

.dashboard-card{
    border: 1px solid #2b2bd0;
}

.dashboard-card-wrapper{
    padding: 5px;
}

.dashboard-card-header{
    text-align: center;
    position: relative;
    margin-bottom: 5px;
}

.dashboard-card-header:after{
    content: '';
    width: 100%;
    height: 1px;
    background-color: #000;
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    margin-top: 5px;
}

.dashboard-card-content-item{
    display: flex;
    justify-content: space-between;
}

.dashboard-card-content-remove {
    z-index: 10;
    width: 32px;
    height: 20px;
    opacity: 0;
    position: relative;
}

.dashboard-card-content-remove:hover {
    cursor: pointer;
    opacity: 1;
}

.dashboard-card-content-remove:before, .dashboard-card-content-remove:after {
    position: absolute;
    left: 15px;
    content: ' ';
    height: 20px;
    width: 2px;
    background-color: #000;
}

.dashboard-card-content-remove:hover .dashboard-card-content-remove:before, .dashboard-card-content-remove:after{
    display: block;
}

.dashboard-card-content-remove:before {
    transform: rotate(45deg);
}

.dashboard-card-content-remove:after {
    transform: rotate(-45deg);
}