.nav{
    position: fixed;
    left: 0;
    bottom: 0;
    top: 30%;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    width: 16%;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    margin-right: auto;
}
.homepage-content{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 96vw;
    height: 68vh;
    margin-left: auto;
}

.homepage-container h1{
    margin: 0;
}

.homepage-form{
    display: flex;
    flex-direction: column;
    margin-top: 10px;
}

.homepage-form button{
    margin: 0 auto;
    margin-top: 10px;
}

.homepage-form-error{
    position: relative;
    margin-top: 5px;
    text-align: center;
    font-size: 18px;
    color: #ec2a2a;
}
.register-container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 99vw;
    height: 60vh;
    margin-left: auto;
}

.register-form{
    display: block;
    display: flex;
    flex-direction: column;
    margin-top: 10px;
}

.register-form button{
    margin-top: 5px;
}

.register-form a{
    text-align: center;
    margin-top: 5px;
}

.register-form-error{
    position: relative;
    margin-top: 5px;
    text-align: center;
    font-size: 18px;
    color: #ec2a2a;
}
.dashboards-content{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 83%;
  margin-left: auto;
}

.dashboards-items{
  display: flex;
  flex-wrap: wrap;
  max-width: 100%;
  margin: 0 auto;
  justify-content: flex-end;
}

.dashboards-item{
  position: relative;
  width: 10em;
  margin: 10px;
  height: 7em;
  background-color: #2626d8;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1;
}

.dashboards-item a{
  color: #fff;
  text-decoration: none;
  font-size: 20px;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.dashboards-item:focus{
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}

.dashboards-item:hover{
  cursor: pointer;
}

.dashboards-item-close {
  z-index: 10;
  position: absolute;
  right: 0;
  top: 5px;
  width: 32px;
  height: 20px;
  opacity: 0.5;
  }

.dashboards-item-close:hover {
  cursor: pointer;
  opacity: 1;
}
.dashboards-item-close:before, .dashboards-item-close:after {
  position: absolute;
  left: 15px;
  content: ' ';
  height: 20px;
  width: 2px;
  background-color: #fff;
}

.dashboards-item-close:before {
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
}

.dashboards-item-close:after {
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
}

.dashboards-error{
  position: relative;
  margin-top: 5px;
  text-align: center;
  font-size: 18px;
  color: #ec2a2a;
}

.dashboard-card{
  margin-left: 5px;
}

.dashboard-content{
  display: flex;
  flex-wrap: wrap;
}
.dashboard-content{
    display: flex;
}

.dashboard-create{
    width: 8em;
    max-height: 3em;
    border: none;
    margin-left: 5px;
    border-radius: 5px;
}

.dashboard-card-create{
    margin-top: 0.5em;
    max-width: 18em;
    margin-bottom: 10px;
    border-radius: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 5px;
}

.dashboard-card-create-wrapper{
    padding: 5px;
}

.dashboard-card-create-header{
    border-radius: 5px;
    width: 100%;
    height: 100%;
    background-color: #2b2bd0;
    max-height: 2em;
}

.dashboard-card-create input{
    width: 98%;
    border-radius: 3px;
    border: none;
    padding: 2px;
}

.dashboard-card{
    border: 1px solid #2b2bd0;
}

.dashboard-card-wrapper{
    padding: 5px;
}

.dashboard-card-header{
    text-align: center;
    position: relative;
    margin-bottom: 5px;
}

.dashboard-card-header:after{
    content: '';
    width: 100%;
    height: 1px;
    background-color: #000;
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    margin-top: 5px;
}

.dashboard-card-content-item{
    display: flex;
    justify-content: space-between;
}

.dashboard-card-content-remove {
    z-index: 10;
    width: 32px;
    height: 20px;
    opacity: 0;
    position: relative;
}

.dashboard-card-content-remove:hover {
    cursor: pointer;
    opacity: 1;
}

.dashboard-card-content-remove:before, .dashboard-card-content-remove:after {
    position: absolute;
    left: 15px;
    content: ' ';
    height: 20px;
    width: 2px;
    background-color: #000;
}

.dashboard-card-content-remove:hover .dashboard-card-content-remove:before, .dashboard-card-content-remove:after{
    display: block;
}

.dashboard-card-content-remove:before {
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
}

.dashboard-card-content-remove:after {
    -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg);
}
